* {
    box-sizing: border-box;
}

@font-face {
    font-family: Barlow;
    src: url('../../fonts/Barlow/Barlow-Regular.e7eb634d040a6f384033');
}

.gne-nav {
    position: absolute;
    bottom: 0;
    width: 100vw;
    height: 66.5px;
}

.gne-nav-btn-container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: start;
}

.gne-nav-btn-container img {
    height: 100%;
}

.gne-nav-btn-container > div {
    display: flex;
    align-items: center;
    justify-content: center;
}

/* .gne-nav-btn-container > img.one-liner {
    width: 100%;
} */

.gne-bkg {
    width: 100%;
    height: 100%;
    background-image: url('../../img/global/Global_nav_bkg.png');
    background-size: 100% 100%;
}

.nav-text {
    font-family: 'Barlow';
    font-weight: 400;
    font-size: 1.5dvh;
    text-wrap: wrap;
    text-align: center;
    width: 85%;
}

.nav-text-cont {
    width: 7.15vw;
    height: 100%;
}

.nav-text-1 {
    width: 10.5vw;
}

.nav-img-cont {
    width: 8.3vw;
    height: 100%;
}

.nav-font-small {
    font-size: 1.2dvh;
}

.nav-underline {
    padding-bottom: 1px;
    border-bottom: 1px solid #00DED0;
}

.nav-font-small-wide {
    width: 9.5vw;
}

.nav-manufacturing {
    margin-left: 9vw;
}

.nav-active {
    background-color: #92D050;
    margin-left: -1px;
    /* width: calc(7.1vw + 3px); */
}
.nav-img-cont.nav-active {
    width: 8.3vw;
}

/* .nav-text-1.nav-active {
    width: calc(10.5vw + 3px);
} */

.nav-pi-width {
    width: 4vw;
}

.nav-isi-width {
    width: 100%;
}

a {
    color: black;
    text-decoration: none;
}

img.logo {
    height: 50%;
}