.main-content {
    width: 100vw;
    height: 100dvh;
    background-image: url('../../img/Safety_content.png');
    background-size: 100% 100%;
    position: relative;
}

.main-content > * {
    margin-bottom: 18px;
    margin-left: 5vw;
}

.main-content-img {
    width: 56%;
    margin-top: 5dvh;
}

.arrow {
    position: absolute;
    aspect-ratio: 1;
    top: 66dvh;
    width: 51px;
}

@media screen and (aspect-ratio:1194/834) {
    .arrow {
        width: 45px;
    }
}

.arrow-right {
    right: 4vw;
    background-image: url('../../img/global/Next_right_btn.png');
    background-size: contain;
    background-repeat: no-repeat;
}

.arrow-right:hover:active, .arrow-right:focus-visible {
    background-image: url('../../img/global/Next_right_active_btn.png');
    background-size: contain;
    background-repeat: no-repeat;
}

.arrow-left {
    left: 34vw;
    background-image: url('../../img/global/Previous_left_btn.png');
    background-size: contain;
    background-repeat: no-repeat;
}

.arrow-left:hover:active, .arrow-left:focus-visible {
    background-image: url('../../img/global/Previous_left_active_btn.png');
    background-size: contain;
    background-repeat: no-repeat;
}

.GNE_logo {
    display: block;
    width: 7.5vw;
}

.job_code {
    width: 34vw;
}

@media screen and (aspect-ratio: 16 / 9) {
    .main-content {
        background-image: url('../../img/Safety-7680x4094px-@2x.png');
        background-size: 100%;
    }
    .arrow {
        top: 87dvh;
    }
    .arrow-left {
        left: 27vw;
    }
    .arrow-right {
        right: 2vw;
    }
}