.pop-close {
    position: absolute;
    top: 3.5%;
    right: 4%;
    width: 65px;
    aspect-ratio: 1;
    background-image: url('../../img/global/Close_navy_active.png');
    background-size: contain;
    opacity: 0;
}

.pop-close:hover:active, .pop-close:focus-visible {
    opacity: 1;
}

.manuf-cont-container .pop-close {
    background-image: url('../../img/global/Close_white_active.png');
}

@media screen and (aspect-ratio: 1194 / 834) {
    .pop-close {
        width: 65px;
    }

    .pop-close-Navy {
        width: 65px;
    }

    .manuf-cont-container .pop-close {
        top: 3%;
        right: 3.5%;
    }
}

@media screen and (aspect-ratio: 1366 / 1024) {
    .pop-close {
        width: 65px;
    }

    .pop-close-Navy {
        width: 65px;
    }
}