.video-container {
    position: absolute;
    right: 5vw;
    top: 19dvh;
    width: 44.75vw;
    height: 41.75dvh;
    display: flex;
    align-items: center;
    background-color: black;
}

@media screen and (aspect-ratio: 1194 / 834) {
    .video-container {
        right: 4.5vw;
        width: 42vw;
    }
}

.video {
    max-width: 100%;
    max-height: 100%;
}

.noPacity {
    opacity: 0;
}

.vid-button {
    position: absolute;
    right: 5vw;
    top: 19dvh;
    width: 44.75vw;
    height: 41.75dvh;
}