.circle {
    position: absolute;
    top: 21dvh;
    width: 31%;
    aspect-ratio: 1;
    border-radius: 50%;
    display: flex;
    justify-content: center;
}

.aesis_btn {
    left: 34.5vw;
    background-image: url('../../img/Safety_circle_1.png');
    background-size: contain;
    background-repeat: no-repeat;
}
.aesis_btn:active {
    background-image: url('../../img/Safety_circle_1_active.png');
}

.aes_btn {
    left: 60vw;
    background-image: url('../../img/Safety_circle_2.png');
    background-size: contain;
    background-repeat: no-repeat;
}
.aes_btn:active {
    background-image: url('../../img/Safety_circle_2_active.png');
}

.safety-plus-btn {
    background-image: url('../../img/global/plus_btn.png');
    background-size: 100%;
    height: 5dvh;
    margin-top: 4dvh;
    aspect-ratio: 1;
}

.circle:hover:active > .safety-plus-btn,
.circle:focus-visible > .safety-plus-btn {
    background-image: url('../../img/global/plus_active_btn.png');
}

@media screen and (aspect-ratio: 16 / 9) {
    .aesis_btn {
        left: 32.5vw;
    }
    .aes_btn {
        left: 58vw
    }
}