.popup {
    position: relative;
    left: 0vw;
    top: -100dvh;
    width: 100vw;
    height: 100dvh;
    background-color: rgb(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.ref-cont-container {
    position: relative;
}

.width-2456 {
    max-height: 89.9dvh;
    max-width: 89.9vw;
}

.ref-bkg {
    background-image: url('../../img/Reference_popup.d68f5b362ab4eeb13072');
}

.isi-header {
    position: absolute;
    top: 11%;
    left: 5vw;
    width: 40.6vw;
}

.isi-scroll-container {
    position: absolute;
    top: 22%;
    left: 5vw;
    width: 90%;
    height: 75%;
    overflow-y: scroll;
}

.isi-scroll-img {
    width: 98%;
}

.arrows-container {
    position: absolute;
    width: 100%;
    height: 17%;
    bottom: 0dvh;
}

.pop-arrow {
    position: absolute;
    top: 50%;
    width: 51px;
    height: 51px;
}

@media screen and (aspect-ratio:1194/834) {
    .pop-arrow {
        width: 45px;
    }
}

.pop-arrow-left {
    transform: translateY(-50%);
    left: 4%;
    background-image: url('../../img/global/Previous_left_btn.png');
    background-size: contain;
    background-repeat: no-repeat;
}

.pop-arrow-left:hover:active, .pop-arrow-left:focus-visible {
    background-image: url('../../img/global/Previous_left_active_btn.png');
    background-size: contain;
    background-repeat: no-repeat;
}

.pop-arrow-right {
    transform: translateY(-50%);
    right: 4%;
    background-image: url('../../img/global/Next_right_btn.png');
    background-size: contain;
    background-repeat: no-repeat;
}

.pop-arrow-right:hover:active, .pop-arrow-right:focus-visible {
    background-image: url('../../img/global/Next_right_active_btn.png');
    background-size: contain;
    background-repeat: no-repeat;
}

.link {
    width: 15%;
    height: 2dvh;
    position: absolute;
}

.fda-link {
    top: 292%;
    left: 37%;
}

.pi-link {
    top: 297%;
    left: 44%;
}